import React from "react";
import PropTypes from "prop-types";
import { StaticQuery, graphql } from "gatsby";
import Nav from "./nav";
import Seo from "./seo";
import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import SidePanels from './sidePanels'
import "@fontsource/noto-sans-tc/300.css"

const Layout = ({ children, seo, containerMaxWidth }) => (
  <StaticQuery
    query={graphql`
      query {
        strapiHomepage {
          seo {
            metaTitle
            metaDescription
            shareImage {
              publicURL
            }
          }
        }
      }
    `}
    render={(data) => (
      <div style={{fontFamily: "Noto Sans TC" }}>
        <Seo seo={seo} />
        <Nav containerMaxWidth={containerMaxWidth}/>

        <Container style={containerMaxWidth?{maxWidth:containerMaxWidth+'px'}:{}}>
          <Row className="mt-5">
            {/* left Content/List column */}
            <Col md={9}>
              {children}
            </Col>

            {/* right SidePanels column */}
            <Col md={3}>
              <SidePanels/>
            </Col>
          </Row>
        </Container>
      </div>
    )}
  />
);

Layout.propTypes = {
  children: PropTypes.node.isRequired,
};

export default Layout; 