import React from 'react';
import Card from 'react-bootstrap/Card';
import {Link, StaticQuery, useStaticQuery, graphql} from 'gatsby';
import Badge from 'react-bootstrap/Badge';

const gql = graphql`
    query {
        allStrapiCategory {
        edges {
            node {
                    slug
                    name
                }
            }
        }
        
        allStrapiTag {
            edges {
                node {
                        slug
                        tagName
                }
            }
        }

        allStrapiArticle(  filter: { status: { eq: "published" } }  ) {
            edges {
              node {
                category { 
                    slug
                    name 
                }
                tags{
                  slug
                  tagName
                }
              }
            }
          }
    }    
`;

export default (props)=>{
    const data = useStaticQuery(gql);
    let catSlugArticleCnt ={};
    if( data.allStrapiArticle.edges ){
        catSlugArticleCnt = data.allStrapiArticle.edges.reduce( 
            (acc,cur) =>{
                if( acc[cur.node.category.slug] ){
                    acc[cur.node.category.slug] += acc[cur.node.category.slug];
                }else{
                    acc[cur.node.category.slug]=1;
                }
                return acc;
            }
         , {});
    }


    return (<div className="pt-3">
                <StaticQuery
                    query={gql}
                    render={(data) => {
                        return (<>
                                    <Card border="secondary" className="my-4 d-sm-none d-md-block">
                                        <Card.Header>文 章 分 類</Card.Header>
                                        <Card.Body>
                                            <ul>
                                            {
                                                data.allStrapiCategory.edges.map( e=>{
                                                    return (
                                                        <li className="mb-3" key={e.node.name}>
                                                            <Link to={`/category/${e.node.slug}`}>
                                                                {`${e.node.name}` } 
                                                                <small> {catSlugArticleCnt[e.node.slug]?`(${catSlugArticleCnt[e.node.slug]})`:''} </small>
                                                            </Link>
                                                        </li>
                                                    )
                                                })
                                            }
                                            </ul>
                                        </Card.Body>
                                    </Card>

                                    <Card border="secondary" className="my-4">
                                        <Card.Header>文 章 標 籤</Card.Header>
                                        <Card.Body>
                                        { 
                                            data.allStrapiTag.edges.map( e=>{
                                                return (
                                                    <Badge pill variant="primary" 
                                                        className="mx-1 my-3 px-3 text-wrap" style={{fontSize:'18pt'}}
                                                        as={Link} to={`/tag/${e.node.slug}`} key={e.node.tagName}>
                                                        {`${e.node.tagName}`}
                                                    </Badge>
                                                )
                                            })
                                        } 
                                        </Card.Body>    
                                    </Card>    
                            </>
                        )
                    }}
                />

    </div>);
}